import React from 'react';
import ParserHTML from '../components/particles/ParseHTML';
import { graphql } from 'gatsby';
import Image from 'gatsby-image';
import Layout from './layout';
import Meta from './meta';


export const query = graphql`
    query($slug: String!) {
        allDatoCmsArticulo(filter: { slug: { eq: $slug } }) {
            nodes {
                    titulo
                    id
                  descripcion
                  seoMetaTags {
                    tags
                  }
                categoria
                  tag
                  imgdestacada {
                    fluid {
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
`

const ArticuloTemplate = ({ data: { allDatoCmsArticulo: { nodes } } }) => {

    const { imgdestacada, descripcion,  titulo, tag, categoria, seoMetaTags } = nodes[0];

    return (
        <Layout>
            <Meta
                title={seoMetaTags.tags[0].content}
                description={seoMetaTags.tags[3].attributes.content}
                image={seoMetaTags.tags[13].attributes.content}
            />
            <main className="w-2/3 mx-auto my-8 list-none">
                <h1 className="text-center m-8">{titulo}2</h1>
                <Image className="w-full max-w-screen-md mx-auto"
                    fluid={imgdestacada.fluid}
                />
                <p>{ParserHTML(descripcion)} </p>
                <br></br><br></br><b>Tag: </b>{tag}
                <br></br><br></br><b>Categoria: </b>
                <b>{categoria}</b>
            </main>
        </Layout>
    );
}

export default ArticuloTemplate;